<template>
  <div v-loading="loading">
    <div
      class="echart"
      ref="pie"
      style="width: 100%;height: 610px;"
    ></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import theme from "@/assets/vintage.json";
import axios from "@/util/AxiosUtil";
import apis from "@/util/ApiUrls";
export default {
  name: "CategoryComponent",
  created() {
    // -JSON
    echarts.registerTheme("vintage", theme);
    // 
    this.$EventBus.$on("pie_category_changed", () => {
      // 
     this.getData(this.refreshData);
    })
  },
  mounted() {
    this.getData(this.initChart);
  },
  methods: {
    // 
    getData(initFunc) {
      // 
    axios
      .get(apis.api_article.categoryGroup)
      .then((res) => {
        let list = res.data;
        let chartData = [];
        // 
        list.forEach((element) => {
          chartData.push({
            name: element.categoryName,
            value: element.noteCount,
          });
        });
        // 
        initFunc(chartData);
      })
      .catch((e) => {
        this.$notify({
          title: "",
          message: ":" + e.msg,
          type: "warning",
          position: 'bottom-right',
          duration: 1500,
        });
        this.$refs.pie.innerHTML =
          '<div style="position: relative;top:40%"><div>';
        this.loading = false;
      });
    },
    // 
    initChart(chartData) {
      let chart = echarts.init(this.$refs.pie, "vintage", {
        height: "610px",
      });

      this.options.series[0].data = chartData;
      chart.setOption(this.options);
      //  - 
      window.onresize = () => {
        console.log("window resized...");
        chart.resize();
      };

      // 
      this.loading = false;
      this.chart = chart;
    },
    // 
    refreshData(chartData) {
      this.options.series[0].data = chartData;
      this.chart.setOption(this.options);
    },
  },
  data() {
    return {
      chart: undefined,
      loading: true,
      options: {
        tooltip: {
          trigger: "item",
        },
        legend: {
          // 
          type: "scroll",
          orient: "horizontal",
          bottom: 5,
        },
        series: [
          {
            name: "",
            type: "pie",
            stillShowZeroSum: false, // 0 - false: 
            label: {
              show: false, // 0 - false: 
            },
            radius: "60%",
            data: [],
          },
        ],
      }
    };
  },
};
</script>

<style scoped>
.el-loading-spinner {
  margin-top: 40% !important;
}
</style>
