<template>
  <div id="userDetail">
    <el-row :gutter="5" type="flex" v-loading="loading">
      <el-col :span="13">
        <el-card
          class="box-card"
          style="border-radius: 10px; background-color: #ffffffad"
        >
          <el-descriptions title="" :column="2" border>
            <template slot="extra">
              <el-button
                type="success"
                size="small"
                plain
                round
                @click="showUpdateDialog"
                ></el-button
              >
              <el-button
                type="warning"
                size="small"
                plain
                round
                @click="initResetDialog"
                ></el-button
              >
            </template>
            <el-descriptions-item
              label=""
              label-class-name="my-label"
              content-class-name="my-content"
              >{{ nickName }}</el-descriptions-item
            >
            <el-descriptions-item
              label=""
              label-class-name="my-label"
              content-class-name="my-content"
              >{{ mail }}</el-descriptions-item
            >
            <el-descriptions-item
              label=""
              label-class-name="my-label"
              content-class-name="my-content"
              >{{ sex }}</el-descriptions-item
            >
          </el-descriptions></el-card
        >

        <!--  -->
        <el-card
          v-if="showUsers"
          class="box-card"
          style="
            border-radius: 10px;
            background-color: #ffffffad;
            margin-top: 5px;
            padding: 10px;
            height: 467px;
          "
        >
          <el-badge :value="registerNum" class="item" type="warning">
            <el-button
              size="small"
              @click="unconfirmVisible = true"
              type="warning"
              plain
              ></el-button
            >
          </el-badge>
          <el-button
            style="float: left"
            size="small"
            @click="generateInviteCode"
            type="success"
            plain
            ></el-button
          >
          
          <div
            id="searchGroup"
            style="
              float: right;
              width: 220px;
              height: 14px;
              font-size: 13px;
              margin-bottom: 2px;
            "
          >
            <el-input
              id="searchAccount"
              placeholder=""
              v-model="searchAccount"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="searchNormalAccount"
              ></el-button>
            </el-input>
          </div>
          <el-table
            height="384"
            :data="userList"
            style="width: 100%; background-color: #ffffffad"
          >
            <el-table-column
              prop="createTime"
              label=""
              width="129"
              header-align="center"
              align="center"
            >
            </el-table-column>
            <el-table-column prop="account" label="" width="240"> </el-table-column>
            <el-table-column prop="mail" label=""> </el-table-column>
            <el-table-column
              label="/"
              width="150"
              align="center"
              header-align="center"
            >
              <template slot-scope="scope">
                <el-switch
                  @change="changeSwitch(scope.row)"
                  v-model="scope.row.locked"
                  active-color="#ff6300"
                  inactive-color="#cfe9d0"
                >
                </el-switch
                >&nbsp;
              </template>
            </el-table-column>
          </el-table>
          <br />
          <el-pagination
            background
            layout="prev, pager, next"
            :total="pager.total"
            :current-page="pager.currentPage"
            @current-change="currentPageChange"
            :hide-on-single-page="hidePager"
            :page-size="7"
          >
          </el-pagination>
        </el-card>
      </el-col>
      <el-col :span="11"
        ><el-calendar
          v-model="date"
          style="border-radius: 10px; background-color: #ffffffad"
        >
        </el-calendar>
      </el-col>
    </el-row>

    <el-dialog
      :close-on-click-modal="false"
      title=""
      :visible.sync="updateDialog"
      :append-to-body="true"
      width="330px"
    >
      <el-divider content-position="left"></el-divider>
      <el-input
        v-model="updNickName"
        autocomplete="off"
        :placeholder="nickName"
        prefix-icon="el-icon-user"
      ></el-input>
      <el-divider content-position="left"></el-divider>
      <el-input
        v-model="updMail"
        autocomplete="off"
        :placeholder="mail"
        prefix-icon="el-icon-message"
      ></el-input>
      <el-divider content-position="left"></el-divider>
      <el-radio-group v-model="updSexValue" size="mini">
        <el-radio-button label="1">(^_)</el-radio-button>
        <el-radio-button label="0">()</el-radio-button>
        <el-radio-button label="">(.)</el-radio-button>
      </el-radio-group>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateDialog = false" size="small" plain round
          > </el-button
        >
        <el-button
          type="primary"
          @click="updateUserDetail"
          size="small"
          plain
          round
          > </el-button
        >
      </div>
    </el-dialog>

    <el-dialog
      :close-on-click-modal="false"
      title=""
      :visible.sync="resetDialog"
      :append-to-body="true"
      width="330px"
    >
      <el-divider content-position="left"></el-divider>
      <el-input
        v-model="mailCode"
        autocomplete="off"
        placeholder=",_"
        :disabled="mailCodeDisabled"
        prefix-icon="el-icon-message"
        maxlength="6"
        show-word-limit
      >
        <el-button
          slot="append"
          icon="el-icon-s-promotion"
          @click="sendMailCode"
        ></el-button>
      </el-input>
      <el-divider content-position="left"></el-divider>
      <el-input
        v-model="newPassword"
        autocomplete="off"
        placeholder=""
        prefix-icon="el-icon-key"
        type="password"
      ></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetDialog = false" size="small" plain round
          > </el-button
        >
        <el-button
          type="primary"
          @click="resetPassword"
          size="small"
          plain
          round
          > </el-button
        >
      </div>
    </el-dialog>

    <!-- -->
    <el-dialog
      width="750px"
      title=""
      :visible.sync="unconfirmVisible"
      :append-to-body="true"
    >
      <el-input
        id="unConfirmedAccount"
        placeholder=""
        v-model="unConfirmedAccount"
        style="
          float: left;
          width: 220px;
          height: 14px;
          font-size: 13px;
          margin-left: 10px;
        "
      >
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="searchUnConfirmedAccount"
        ></el-button>
      </el-input>
      <el-button icon="el-icon-refresh-left" @click="clearSearch"></el-button>
      <el-table :data="unconfirmedUsers" height="520">
        <el-table-column
          property="createTime"
          label=""
          width="140"
        ></el-table-column>
        <el-table-column
          property="account"
          label=""
          width="200"
        ></el-table-column>
        <el-table-column property="mail" label=""></el-table-column>
        <el-table-column label="" align="center" width="80">
          <template slot-scope="scope">
            <el-button
              icon="el-icon-check"
              circle
              plain
              size="mini"
              type="success"
              @click="accept(scope.row.id)"
            ></el-button>
            <el-popconfirm
              icon-color="red"
              @confirm="deny(scope.row.id)"
              title=",?"
            >
              <el-button
                type="danger"
                icon="el-icon-close"
                circle
                size="mini"
                slot="reference"
              ></el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import storageUtil from "@/util/StorageUtil";
import axios from "@/util/AxiosUtil";
import apis from "@/util/ApiUrls";
import axiosStatic from "axios";

/**
 * 
 */
export default {
  name: "UserComponent",
  created() {
    const metaData = storageUtil.get();
    // 
    this.showUsers = metaData.auth === "1";
    this.loading = true;
    axios
      .get(apis.api_auth.user)
      .then((user) => {
        // 
        this.nickName = user.data.nickName;
        this.updNickName = user.data.nickName;
        this.mail = user.data.mail;
        this.updMail = user.data.mail;
        this.sex = user.data.sex;
        this.sexValue = user.data.sexValue;
        this.updSexValue = user.data.sexValue;
      })
      .catch((e) => {
        this.$notify({
          title: ":",
          message: e.msg,
          type: "warning",
          duration: 1300,
        });
      })
      .finally(() => {
        this.loading = false;
      });

    // -99
    // 
    if (this.showUsers) {
      this.getUsersTable();
    }
  },
  methods: {
    // 
    getUsersTable() {
      this.loading = true;
      // 
      axiosStatic
        .all([
          axios.get(apis.api_auth.unconfirmedUsers),
          axios.get(apis.api_auth.users),
        ])
        .then(
          axiosStatic.spread((unconfirmedUsers, users) => {
            console.log(unconfirmedUsers);

            this.registerNum =
              unconfirmedUsers.data.length > 0
                ? unconfirmedUsers.data.length
                : "";
            this.unconfirmedUsers = unconfirmedUsers.data;
            this.cacheUsers = unconfirmedUsers.data;

            // 
            this.userList = users.data.userList ?? [];
            // 
            this.pager.total = users.data.total;
          })
        )
        .catch((e) => {
          this.$notify({
            title: ":",
            message: e.msg,
            type: "warning",
            duration: 1300,
          });
        })
        .finally(() => {
          this.loading = false;
          if (this.userList.length > 0) {
            // 
            this.hidePager = false;
          } else {
            this.hidePager = true;
          }
        });
    },
    // 
    currentPageChange(currentPage) {
      this.pager.currentPage = currentPage;
      this.searchAccountTable();
    },
    // 
    searchNormalAccount() {
      this.searchAccountTable();
    },
    // 
    searchAccountTable() {
      this.loading = true;
      axios
        .get(
          apis.api_auth.users +
            "?pageIndex=" +
            this.pager.currentPage +
            "&searchAccount=" +
            this.searchAccount
        )
        .then((users) => {
          this.userList = users.data.userList;
          if (!this.searchAccount) {
            this.pager.total = users.data.total;
          } else {
            this.pager.total = 0;
          }
        })
        .catch((e) => {
          this.$notify({
            title: ":",
            message: e.msg,
            type: "warning",
            duration: 1300,
          });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    // 
    clearSearch() {
      this.unConfirmedAccount = "";
      // 
      this.unconfirmedUsers = this.cacheUsers;
    },
    // 
    searchUnConfirmedAccount() {
      if (
        this.unConfirmedAccount === undefined ||
        this.unConfirmedAccount.trim().length === 0
      ) {
        this.$message({
          message: "",
          type: "warning",
          duration: 1300,
        });
        return;
      }
      axios
        .get(
          apis.api_auth.unconfirmedUsers +
            "?unconfirmedAccount=" +
            this.unConfirmedAccount
        )
        .then((user) => {
          this.unconfirmedUsers = user.data;
        })
        .catch((e) => {
          this.$notify({
            title: ":",
            message: e.msg,
            type: "warning",
            duration: 1300,
          });
        });
    },
    // 
    accept(id) {
      this.loading = true;
      axios
        .post(apis.api_auth.accept + "?id=" + id)
        .then(() => {
          this.$notify({
            title: "",
            type: "success",
            duration: 1300,
          });
          // 
          this.getUsersTable();
        })
        .catch((e) => {
          this.$notify({
            title: ":",
            message: e.msg,
            type: "warning",
            duration: 1300,
          });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    // 
    deny(id) {
      this.loading = true;
      axios
        .delete(apis.api_auth.deny + "?id=" + id)
        .then(() => {
          this.$notify({
            title: "",
            type: "success",
            duration: 1300,
          });
          // 
          this.getUsersTable();
        })
        .catch((e) => {
          this.$notify({
            title: ":",
            message: e.msg,
            type: "warning",
            duration: 1300,
          });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    // 
    generateInviteCode() {
      this.loading = true;
      axios
        .get(apis.api_auth.invitationCode)
        .then((res) => {
          this.$alert(
            ": " + res.data,
            "60,",
            {
              confirmButtonText: "",
            }
          );
        })
        .catch((e) => {
          this.$notify({
            title: ":",
            message: e.msg,
            type: "warning",
            duration: 1300,
          });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    // /
    changeSwitch(row) {
      let locked = row.locked;
      // -
      if (locked) {
        row.locked = false;
        this.$confirm(",, ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        })
          .then(() => {
            this.loading = true;
            axios
              .put(apis.api_auth.lockUser, { id: row.id })
              .then(() => {
                this.$notify({
                  title: "",
                  type: "success",
                  duration: 1300,
                });
                row.locked = true;
              })
              .catch((e) => {
                this.$notify({
                  title: ":",
                  message: e.msg,
                  type: "warning",
                  duration: 1300,
                });
              })
              .finally(() => {
                this.loading = false;
              });
          })
          .catch(() => {
            row.locked = false;
          });
      } else {
        // 
        row.locked = true;
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "info",
        })
          .then(() => {
            this.loading = true;
            axios
              .put(apis.api_auth.unlockUser, { id: row.id })
              .then(() => {
                this.$notify({
                  title: "",
                  type: "success",
                  duration: 1300,
                });
                row.locked = false;
              })
              .catch((e) => {
                this.$notify({
                  title: ":",
                  message: e.msg,
                  type: "warning",
                  duration: 1300,
                });
              })
              .finally(() => {
                this.loading = false;
              });
          })
          .catch(() => {
            row.locked = true;
          });
      }
    },
    // dialog
    showUpdateDialog() {
      this.updNickName = this.nickName;
      this.updMail = this.mail;
      this.updSexValue = this.sexValue;
      this.updateDialog = true;
    },
    // 
    initResetDialog() {
      this.resetDialog = true;
      this.mailCode = "";
      this.newPassword = "";
    },
    // 
    resetPassword() {
      if (!this.mailCode || this.mailCode.trim().length === 0) {
        this.$message({
          message: "~",
          type: "warning",
          duration: 1500,
        });
        return;
      }

      if (!this.newPassword || this.newPassword.trim().length === 0) {
        this.$message({
          message: "~",
          type: "warning",
          duration: 1500,
        });
        return;
      }

      this.loading = true;
      axios
        .put(apis.api_auth.password, {
          password: this.newPassword,
          code: this.mailCode,
        })
        .then(() => {
          this.$notify({
            title: ",",
            type: "success",
            duration: 1300,
          });

          // 
          this.mailCodeDisabled = true;
          // 
          this.newPassword = "";
          this.mailCode = "";
          this.resetDialog = false;
        })
        .catch((e) => {
          this.$notify({
            title: ":",
            message: e.msg,
            type: "warning",
            duration: 1300,
          });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    // 
    sendMailCode() {
      this.loading = true;
      axios
        .post(apis.api_auth.mailCode)
        .then(() => {
          this.$notify({
            title: "" + this.mail + ",",
            type: "success",
            duration: 1300,
          });
          // 
          this.mailCodeDisabled = false;
        })
        .catch((e) => {
          this.$notify({
            title: ":",
            message: e.msg,
            type: "warning",
            duration: 1300,
          });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    updateUserDetail() {
      // check
      if (
        this.updNickName === undefined ||
        this.updNickName.trim().length === 0
      ) {
        this.$message({
          message: "~",
          type: "error",
          duration: 1500,
        });
        return;
      }
      if (this.updMail === undefined || this.updMail.trim().length === 0) {
        this.$message({
          message: "~",
          type: "error",
          duration: 1500,
        });
        return;
      } else if (
        !/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/.test(this.updMail)
      ) {
        this.$message({
          message: "~",
          type: "error",
          duration: 1500,
        });
        return;
      }
      // 
      if (
        this.updMail === this.mail &&
        this.updNickName === this.nickName &&
        this.updSexValue === this.sexValue
      ) {
        this.updateDialog = false;
        return;
      }

      this.loading = true;
      axios
        .put(apis.api_auth.user, {
          nickName: this.updNickName,
          mail: this.updMail,
          sexValue: this.updSexValue,
        })
        .then((res) => {
          this.nickName = res.data.nickName;
          this.mail = res.data.mail;
          this.sex = res.data.sex;
          this.sexValue = res.data.sexValue;
          this.updateDialog = false;
        })
        .catch((e) => {
          this.$notify({
            title: ":",
            message: e.msg,
            type: "warning",
            duration: 1300,
          });
        })
        .finally(() => {
          this.loading = false;
        });
    },
  },
  data() {
    return {
      updMail: "", // 
      updNickName: "", // 
      updSexValue: "", // 
      updateDialog: false, // 
      resetDialog: false, // 
      loading: false,
      nickName: storageUtil.getItem("nickname"),
      mail: "",
      sex: "", // 
      sexValue: "", // 
      mailCode: "", // 
      newPassword: "", // 
      mailCodeDisabled: true, // 
      date: new Date(),
      userList: [], // 
      registerNum: "", // 
      showUsers: false, // 
      unconfirmedUsers: [], // 
      unconfirmVisible: false, // 
      unConfirmedAccount: "", // -
      cacheUsers: [], // 
      pager: {
        total: 0,
        currentPage: 1,
      },
      hidePager: false,
      searchAccount: "", // 
    };
  },
};
</script>


<style scope>
#userDetail .my-label {
  background-color: #dcf2f16b !important;
  width: 100px;
}

#userDetail .my-content {
  background: #e2d98533;
  width: 200px;
}

#userDetail .el-dialog__body {
  padding: 0 20px;
}

#userDetail .el-descriptions {
  padding: 10px;
}

#userDetail div.el-card__body .el-badge {
  float: left;
}

#userDetail table tr,
#userDetail table tr th {
  background-color: #ffffff00;
}

#userDetail input#searchAccount {
  height: 32px;
  background-color: #e3fbe9b3;
}

#userDetail #searchGroup .el-input-group__append {
  background-color: #f6edc2;
}
</style>