<template>
  <div id="register">
    <el-divider content-position="left"
      ><i class="el-icon-document light-blue-clor" style="font-size: 30px"></i
      >&nbsp;<font style="font-size: 15px" class="light-blue-clor"
        >()</font
      ></el-divider
    >

    <el-form
      v-loading="loading"
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      :label-position="labelPosition"
      label-width="70px"
    >
      <el-form-item label="" prop="account">
        <el-input
          v-model="ruleForm.account"
          placeholder=""
          autocomplete="off"
          prefix-icon="el-icon-user"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="nickname">
        <el-input
          v-model="ruleForm.nickname"
          placeholder=""
          autocomplete="off"
          prefix-icon="el-icon-magic-stick"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input
          v-model="ruleForm.password"
          type="password"
          placeholder=""
          prefix-icon="el-icon-key"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="checkPassword">
        <el-input
          v-model="ruleForm.checkPassword"
          type="password"
          placeholder=""
          prefix-icon="el-icon-unlock"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="mail">
        <el-input
          v-model="ruleForm.mail"
          placeholder="()"
          prefix-icon="el-icon-message"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="invitationCode">
        <el-input
          v-model="ruleForm.invitationCode"
          placeholder="()"
          prefix-icon="el-icon-share"
        ></el-input>
      </el-form-item>

      <el-form-item style="text-align: right">
        <el-button plain size="mini" round @click="back"
          ><i class="el-icon-arrow-left"></i></el-button
        >

        <el-popconfirm title=", ?" @confirm="reset">
          <el-button size="mini" round slot="reference"></el-button>
        </el-popconfirm>

        <el-button type="primary" size="mini" round @click="register" :disabled="disableBtn"
          ><i class="el-icon-arrow-right"></i
        ></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { URLS } from "@/router";
import axios from "@/util/AxiosUtil";
import apis from "@/util/ApiUrls";
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.ruleForm.checkPassword !== "") {
          this.$refs.ruleForm.validateField("checkPassword");
        }
        callback();
      }
    };

    var validatecheckPass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.ruleForm.checkPassword !== this.ruleForm.password) {
          callback(new Error(""));
        } else {
          callback();
        }
      }
    };

    var validateMail = (rule, value, callback) => {
      if (this.ruleForm.mail === "") {
        callback(new Error(""));
      } else {
        let reg = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
        if (!reg.test(this.ruleForm.mail)) {
          callback(new Error(""));
        } else {
          callback();
        }
      }
    };

    var validateAccount = (rule, value, callback) => {
      if (this.ruleForm.account === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };

    var validateNickName = (rule, value, callback) => {
      if (this.ruleForm.nickname === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };

    return {
      disableBtn: false, // /
      loading: false,
      labelPosition: "left",
      ruleForm: {
        account: "",
        nickname: "",
        password: "",
        checkPassword: "",
        mail: "",
        invitationCode: "",
      },
      rules: {
        account: [{ validator: validateAccount, trigger: "change" }],
        nickname: [{ validator: validateNickName, trigger: "change" }],
        password: [{ validator: validatePass, trigger: "change" }],
        checkPassword: [{ validator: validatecheckPass, trigger: "change" }],
        mail: [{ validator: validateMail, trigger: "change" }],
      },
    };
  },
  methods: {
    reset() {
      this.$notify.info({
        title: "",
        message: "",
        duration: 800,
      });
      this.$refs["ruleForm"].resetFields();
    },
    // 
    back() {
      this.$router.push({
        name: URLS.loginName,
      });
    },
    // 
    register() {
      const _this = () => this;
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          this.loading = true;
          // 
          this.disableBtn = true;
          axios
            .post(apis.api_auth.register, {
              account: this.ruleForm.account,
              password: this.ruleForm.password,
              nickname: this.ruleForm.nickname,
              mail: this.ruleForm.mail,
              invitationCode: this.ruleForm.invitationCode,
            })
            .then(() => {
              this.$notify({
                title: "(  .  )",
                message: ",,",
                type: "success",
                duration: 1200,
                onClose: () => {
                  _this().$router.push({
                    name: URLS.loginName,
                  });
                },
              });
            })
            .catch((e) => {
              this.$message({
                message: ":" + e.msg,
                type: "error",
                duration: 1500,
              });
              // 
              this.disableBtn = false;
            })
            .finally(() => {
              this.loading = false;
            });
        } else {
          return false;
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
#register {
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
  width: 350px;
  height: 450px;
  border: 1px solid #7ebbf7e5;
  box-shadow: 0 2px 12px 0 #7ebbf7e5;
  border-radius: 10px;
  padding: 15px;
  background-color: #ffffff;
}
</style>
