import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import IndexView from '@/views/IndexView.vue';
import LoginView from '@/views/LoginView.vue'
import HomeView from '@/views/HomeView.vue'
import UserView from '@/views/UserView.vue';
import RegisterView from '@/views/RegisterView.vue';
import NotFoundView from '@/views/404View.vue';
import PanelView from '@/views/NotesPanelView.vue';
import NotebookEditorView from '@/views/NotebookEditorView.vue';
import NotebookInfoView from '@/views/NotebookInfoView.vue';
import storageUtil from '@/util/StorageUtil.js';
import axiosInstance from '@/util/AxiosUtil.js';
import apis from '@/util/ApiUrls.js';

Vue.use(VueRouter)

export const URLS = {
  any: "*", // 
  root: "/", // 
  notebook: "/notebook",
  login: "/notebook/login", // 
  loginName: "login",
  register: "/notebook/register", // 
  registerName: "register",
  index: "/notebook/index", // 
  home: "/notebook/main", // 
  homeName: "main",
  new: "/notebook/new", // 
  newName: "new", // 
  edit: "/notebook/edit/:id", // 
  editName: "edit", // 
  view: "/notebook/view/:id", // 
  viewName: "view", // 

  // user
  user: "/notebook/user", // 
  userName: "user"
}

/**
 * 
 */
const routes: Array<RouteConfig> = [
  {
    path: URLS.root,
    redirect: URLS.login,
  },
  {
    path: URLS.register,
    name: URLS.registerName,
    component: RegisterView
  },
  {
    path: URLS.login,
    name: URLS.loginName,
    component: LoginView
  },
  {
    path: URLS.index,
    name: 'index',
    component: IndexView
  },
  {
    path: URLS.home,
    name: URLS.homeName,
    component: HomeView,
    // 
    children: [
      {
        path: 'panel',
        component: PanelView
      },
      {
        path: 'user',
        component: UserView
      }
    ]
  },
  {
    path: URLS.new,
    name: URLS.newName,
    component: NotebookEditorView
  },
  {
    path: URLS.edit,
    name: URLS.editName,
    component: NotebookEditorView
  },
  {
    path: URLS.view,
    name: URLS.viewName,
    component: NotebookInfoView
  },
  {
    path: URLS.any,
    component: NotFoundView
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

/**
 * 
 */
router.beforeEach((to, from, next) => {
  // next() 
  if (to.path === URLS.login || to.path === URLS.register) {
    // 
    next();
  } else {
    const metaData = storageUtil.get();
    if (metaData.isLogin) {

      if (window.refreshInt === undefined) {
        refresh();
        window.refreshInt = window.setInterval(() => {
          // Token
          refresh();
        }, 1000 * 5 * 60); // 5,buffer
      }

      // 
      if (to.path === URLS.notebook || to.path === URLS.notebook + "/") {
        next({
          path: URLS.home
        });
      } else {
        next();
      }
    } else {
      next({
        path: URLS.login
      });
    }
  }

});

/**
 * token
 */
const refresh = () => {
  const metaData = storageUtil.get();
  // -loginMillstoken,token
  const loginMills = parseInt(metaData.loginMills, 10);
  // 20buffer-,
  if (Date.now() - loginMills > (20 * 60 * 1000)) {
    // token-tokentoken
    axiosInstance.post(apis.api_auth.refresh, undefined, { headers: { RefreshToken: metaData.refreshToken } }).then((res: any) => {
      // token
      storageUtil.add("loginMills", res.data.LoginMills);
      storageUtil.add("authorization", res.data.Authorization);
    }).catch((e: any) => {
      // 
      console.log("", e)
    })
  }
}


export default router
