<template>
  <div v-loading.lock="loading" id="categoryManager">
    <el-table
      :data="tableData"
      height="572px"
      style="background-color: #ffffff00"
    >
      <el-table-column prop="categoryName" label=""> </el-table-column>
      <el-table-column
        prop="noteCount"
        label=""
        header-align="center"
        align="center"
        width="80"
      >
      </el-table-column>
      <el-table-column
        label=""
        width="106"
        header-align="center"
        align="center"
      >
        <template slot-scope="scope">
          <el-popover placement="top" width="225" @show="categoryNameMod = ''">
            <el-input
              v-model="categoryNameMod"
              autocomplete="off"
              maxlength="10"
              show-word-limit
              prefix-icon="el-icon-document-add"
              :placeholder="scope.row.categoryName"
            ></el-input>
            <div style="text-align: right; margin-top: 5px">
              <el-button size="mini" type="text" @click="closeEdit"
                ></el-button
              >
              <el-button
                type="success"
                size="mini"
                @click="editCategory(scope.row)"
                ></el-button
              >
            </div>
            <el-button
              type="primary"
              icon="el-icon-edit-outline"
              slot="reference"
              circle
              size="mini"
            ></el-button>
          </el-popover>
          <el-popconfirm
            icon-color="green"
            confirm-button-type="success"
            @confirm="downloadNotes(scope.row)"
            title=",?"
          >
            <el-button
              type="warning"
              icon="el-icon-download"
              circle
              slot="reference"
              size="mini"
              :disabled="scope.row.noteCount == 0"
            ></el-button>
          </el-popconfirm>

          <el-popconfirm
            icon-color="red"
            confirm-button-type="danger"
            @confirm="deleteCategory(scope.row)"
            title=",?"
          >
            <el-button
              type="danger"
              icon="el-icon-minus"
              circle
              slot="reference"
              size="mini"
              :disabled="scope.row.noteCount > 0"
            ></el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>

    <el-popover
      placement="top"
      width="225"
      @show="categoryName = ''"
      v-model="dialogAddVisible"
      ref="addCat"
    >
      <el-input
        v-model="categoryName"
        autocomplete="off"
        maxlength="10"
        show-word-limit
        prefix-icon="el-icon-document-add"
        placeholder=""
      ></el-input>
      <div style="text-align: right; margin-top: 5px">
        <el-button size="mini" type="text" @click="dialogAddVisible = false"
          ></el-button
        >
        <el-button type="primary" size="mini" @click="addCategory"
          ></el-button
        >
      </div>
      <el-button
        type="primary"
        icon="el-icon-plus"
        round
        size="medium"
        slot="reference"
        style="margin-top: 3px;background-color: #00b6d37d;border-color: #e7350085;"
      ></el-button>
    </el-popover>
  </div>
</template>

<script>
import axios from "@/util/AxiosUtil";
import apis from "@/util/ApiUrls";
export default {
  created() {
    this.getData();
     // 
    this.$EventBus.$on("table_category_changed", () => {
      // 
     this.getData();
    })
  },
  methods: {
    // popoverv-model,
    closeEdit() {
      document.body.click();
    },
    // 
    addCategory() {
      if (this.categoryName === undefined || this.categoryName.trim() === "") {
        this.$message({
          message: "",
          type: "warning",
          duration: 1000,
        });
        return;
      }

      // 
      if (this.tableData.length > 25) {
        this.$notify({
          title: "",
          message: "25",
          position: "bottom-right",
          type: "warning",
          duration: 1500,
        });
        return;
      }

      this.loading = true;
      axios
        .post(apis.api_article.category, {
          categoryName: this.categoryName,
          categoryId: undefined,
        })
        .then((res) => {
          // 
          this.$message({
            message: "~",
            type: "success",
            duration: 1200,
          });
          this.dialogAddVisible = false;
          // 
          this.getData();
          // -
          this.$EventBus.$emit("pie_category_changed", undefined);
        })
        .catch((e) => {
          let msg = e.msg;
          if (e.code === 202) {
            msg = "~?";
          }
          this.$notify({
            title: "",
            message: msg,
            type: "warning",
            position: "bottom-right",
            duration: 1500,
          });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    // 
    getData() {
      // 
      axios
        .get(apis.api_article.categoryGroup)
        .then((res) => {
          this.tableData = res.data;
        })
        .catch((e) => {
          this.$notify({
            title: "",
            message: ":" + e.msg,
            type: "warning",
            position: "bottom-right",
            duration: 1500,
          });
        });
    },
    // 
    downloadNotes(row) {
      this.loading = true;
      axios.post(apis.api_article.downloadCategory + "/" + row.categoryId, undefined, {responseType: 'blob'}).then(() => {
        this.$message({
          message: '',
          type: 'success'
        });
      }).catch(e => {
        this.$message({
          message: '',
          type: 'warning'
        });
      }).finally(() => {
        this.loading = false;
      });
    },
    // 
    deleteCategory(row) {
      this.loading = true;
      axios
        .delete(apis.api_article.category + "?categoryId=" + row.categoryId)
        .then((res) => {
          this.$message({
            message: "[" + row.categoryName + "]~",
            duration: 1500,
            type: "success",
          });

          // 
          this.getData();

          // -
          this.$EventBus.$emit("pie_category_changed", undefined);
        })
        .catch((e) => {
          console.log(e);
          this.$notify({
            title: ":",
            message: e.msg,
            type: "warning",
            position: "bottom-right",
            duration: 1500,
          });
        })
        .finally(() => {
          this.loading = false;
        });
    },
    // 
    editCategory(row) {
      this.loading = true;
      axios
        .put(apis.api_article.category, {
          categoryId: row.categoryId,
          categoryName: this.categoryNameMod,
        })
        .then((res) => {
          this.$message({
            message:
              "[" +
              row.categoryName +
              "][" +
              this.categoryNameMod +
              "]~",
            duration: 1500,
            type: "success",
          });
          // 
          this.getData();
          this.closeEdit();
           // -
          this.$EventBus.$emit("pie_category_changed", undefined);
        })
        .catch((e) => {
          console.log(e);
          this.$notify({
            title: ":",
            message: e.msg,
            type: "warning",
            position: "bottom-right",
            duration: 1500,
          });
        })
        .finally(() => {
          this.loading = false;
        });
    },
  },
  data() {
    return {
      loading: false,
      categoryName: "",
      categoryNameMod: "",
      dialogAddVisible: false,
      tableData: [],
    };
  },
};
</script>
<style>
#categoryManager .el-table {
  color: #000000b8;
  font-weight: 600;
  font-size: 15px;
}

#categoryManager .el-table tr {
  background-color: #fff0;
}

#categoryManager .el-table th.el-table__cell {
  background-color: #fff0;
  border-bottom: 2px solid #d5d688 !important;
}

#categoryManager .el-table td.el-table__cell {
  border-bottom: 1px solid #ce8c5833 !important;
}

#categoryManager .el-table::before {
  height: 0px;
}

#categoryManager .el-table thead {
  color: #fb7f00cc;
  font-size: 15px;
}
</style>
