        ></el-table-column>
        <el-table-column property="func" label=""></el-table-column>
      </el-table>
      <br />
      <el-tag type="primary" style="float: right; margin-left: 10px"
        ></el-tag
      >
      <el-tag type="warning" style="float: right"
        ></el-tag
      ><br />
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "EditorHelperComponent",
  props: {
    visible: Boolean,
  },
  data() {
    return {
      // 
      dialogTableVisible: this.visible,
      gridData: [
        {
          shortcut: "F8",
          func: "/",
        },
        {
          shortcut: "F9",
          func: "/",
        },
        {
          shortcut: "F10",
          func: "/",
        },
        {
          shortcut: "F11",
          func: "/",
        },
        {
          shortcut: "F12",
          func: "/",
        },
        {
          shortcut: "CTRL + S",
          func: "",
        },
        {
          shortcut: "CTRL + D",
          func: "",
        },
        {
          shortcut: "CTRL + Z | CTRL + Y",
          func: " | ",
        },
        {
          shortcut: "SHIFT + TAB",
          func: "",
        },
        {
          shortcut: "TAB",
          func: "",
        },
        {
          shortcut: "CTRL + B",
          func: "",
        },
        {
