import axios from 'axios'
import apis from './ApiUrls';
import storageUtil from './StorageUtil';

//  axios 
const axiosInstance = axios.create({
    baseURL: apis.api_host, // url
    timeout: 5000, // 
    // 'Content-Type': 'application/x-www-form-urlencoded',
});

// 
const errHandler = (error) => {
    console.log(error);
    if (!error.response) {
        // 
        return Promise.reject({
            code: 500,
            status: 'INTERNAL_SERVER_ERROR',
            msg: '',
        });
    } else {
        return Promise.reject(error.response);
    }
}

// request interceptor()
axiosInstance.interceptors.request.use(config => {
    const metaData = storageUtil.get();

    if (metaData.authorization) {
        config.headers['Authorization'] = metaData.authorization // token
    }
    if (metaData.userId) {
        config.headers['UserId'] = metaData.userId
    }
    return config
}, errHandler);

// response interceptor
axiosInstance.interceptors.response.use((response) => {
    const res = response.data

    // -- typeblob- response.data,,500
    if (response.headers['md-file']) {
        const blob = new Blob([res], {type: response.headers['content-type']});
        const fileName = response.headers['content-disposition'].replace(/\w+; filename=(.*)/, '$1')
        let dom = document.createElement('a');
        let url = window.URL.createObjectURL(blob);
        dom.href = url;
        dom.download = decodeURI(fileName);
        dom.style.display = 'none'
        document.body.appendChild(dom);
        dom.click();
        dom.parentNode.removeChild(dom);
        window.URL.revokeObjectURL(url);
        return;
    }

    if (res.code !== 200) {

        let msg = res.msg;
        if (res.code === 500) {
            msg = "";
            console.log(res.msg);
        }

        if (res.code === 401) {
            msg = "";
        }

        if (res.code === 403) {
            msg = "";
        }

        if (res.code === 408) {
            msg = "~";
        }

        // 
        if (res.code === 401 || res.code === 403) {
            storageUtil.clear();
        }

        return Promise.reject({
            code: res.code,
            status: res.status,
            msg: msg,
        });
    } else {
        // login
        if (response.headers.authorization && response.headers.refreshtoken && response.headers.userid) {
            const metaData = {
                authorization: response.headers.authorization,
                refreshToken: response.headers.refreshtoken,
                userId: response.headers.userid,
                loginMills: response.headers.loginmills,
                auth: response.headers.userauth,
            }
            storageUtil.set(metaData);
        }
        return Promise.resolve(res);
    }
}, errHandler);




export default axiosInstance;
